/* eslint-disable no-param-reassign */
/* eslint-disable jsx-a11y/label-has-associated-control */
import React, { useState } from 'react';
import classNames from 'classnames';
import ReactAudioPlayer from 'react-audio-player';
import audio1 from '../../audio/Test_08/LR3_32_0.mp3'; // стій *
import audio2 from '../../audio/Test_09/LR9_32_0.mp3'; // стрій
import audio3 from '../../audio/Test_10/LR10_32_-05.mp3'; // бій *
import audio4 from '../../audio/Test_11/LR11_32_-05.mp3'; // дій
import audio5 from '../../audio/Test_12/LR12_32_-10.mp3'; // змій
import noAudio1 from '../../audio/Test_01/No_audio_1.mp3';
import stiy from '../../images/stiy.jpg';
import biy from '../../images/biy.png';
import striy from '../../images/striy.jpg';
import gniy from '../../images/gniy.jpg';
import diy from '../../images/Nike.png';
import zmiy from '../../images/snake.jpg';
import noHear from '../../images/notHear.png';

// import './Test4.scss';

export const Test4:React.FC = () => {
  const test4 = [
    {
      audio: audio1,
      range: 0,
      options: [biy, stiy, diy, noHear],
      meanings: ['Бій', 'Cтій', 'Дій', 'Не чую'],
      correct: stiy,
      choice: '',
    },
    {
      audio: audio2,
      range: 0,
      options: [stiy, diy, striy, noHear],
      meanings: ['Cтій', 'Дій', 'Cтрій', 'Не чую'],
      correct: striy,
      choice: '',
    },
    {
      audio: audio3,
      range: -5,
      options: [biy, zmiy, stiy, noHear],
      meanings: ['Бій', 'Змій', 'Стій', 'Не чую'],
      correct: biy,
      choice: '',
    },
    {
      audio: audio4,
      range: -5,
      options: [striy, biy, diy, noHear],
      meanings: ['стрій', 'Бій', 'Дій', 'Не чую'],
      correct: diy,
      choice: '',
    },
    {
      audio: audio5,
      range: -10,
      options: [gniy, zmiy, stiy, noHear],
      meanings: ['Гній', 'Змій', 'Cтій', 'Не чую'],
      correct: zmiy,
      choice: '',
    },
    {
      audio: noAudio1,
      range: 0,
      options: [stiy, diy, striy, noHear],
      meanings: ['Cтій', 'Дій', 'Cтрій', 'Не чую'],
      correct: noHear,
      choice: '',
    },
  ];

  const [tests, setTests] = useState(test4);

  const handleChoiceChange = (audio: string, choice: string) => {
    const newArr = [...tests].map((test) => {
      if (test.audio === audio) {
        if (test.choice.localeCompare(choice) !== 0) {
          test.choice = choice;
        } else {
          test.choice = '';
        }
      }

      return test;
    });

    setTests(newArr);
  };

  const [isFinished, setIsFinished] = useState(false);
  const [score, setScore] = useState(0);
  const [res, setRes] = useState('');

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    const sumWithInitial = tests.reduce(
      (accumulator, cur) => {
        if (cur.choice === cur.correct) {
          return accumulator + 1;
        }

        return accumulator;
      },
      0,
    );

    const tempL = [10];
    const tempR = [10];

    for (const el of tests) {
      if (el.choice === el.correct && el.correct === 'L') {
        tempL.push(el.range);
      }

      if (el.choice === el.correct && el.correct === 'R') {
        tempR.push(el.range);
      }
    }

    setRes(`Ви чуєте лівим вухом при рівні ${Math.min(...tempL)} та правим ${Math.min(...tempR)} в залежності від звичайного голосу`);

    setScore(sumWithInitial);
    setIsFinished(true);
  };

  return (
    <>
      <h2>Test 4</h2>

      <form className="form" onSubmit={handleSubmit}>
        <div className="form_section">
          <h3>Прослухайте записи і позначте що ви чуєте</h3>
          {tests.map(test => (
            <div
              className={classNames(
                'form_section__question',
                { form_section__question__wrong: test.correct !== test.choice && isFinished },
                { form_section__question__right: test.correct === test.choice && isFinished },
              )}
              key={test.audio}
            >
              <ReactAudioPlayer
                src={test.audio}
                controls
              />

              <div className="form_section__question__answers">
                <div>
                  <input
                    type="radio"
                    id={`${test.audio}_${test.options[0]}`}
                    name={test.audio}
                    value={test.options[0]}
                    onClick={() => handleChoiceChange(test.audio, test.options[0])}
                    checked={test.choice === test.options[0]}
                    required
                  />
                  <label htmlFor={`${test.audio}_${test.options[0]}`}>
                    <img
                      className="image-option"
                      src={test.options[0]}
                      alt={test.meanings[0]}
                    />
                  </label>
                </div>

                <div>
                  <input
                    type="radio"
                    id={`${test.audio}_${test.options[1]}`}
                    name={test.audio}
                    value={test.options[1]}
                    onClick={() => handleChoiceChange(test.audio, test.options[1])}
                    checked={test.choice === test.options[1]}
                    required
                  />
                  <label htmlFor={`${test.audio}_${test.options[1]}`}>
                    <img
                      className="image-option"
                      src={test.options[1]}
                      alt={test.meanings[1]}
                    />
                  </label>
                </div>

                <div>
                  <input
                    type="radio"
                    id={`${test.audio}_${test.options[2]}`}
                    name={test.audio}
                    value={test.options[2]}
                    onClick={() => handleChoiceChange(test.audio, test.options[2])}
                    checked={test.choice === test.options[2]}
                    required
                  />
                  <label htmlFor={`${test.audio}_${test.options[2]}`}>
                    <img
                      className="image-option"
                      src={test.options[2]}
                      alt={test.meanings[2]}
                    />
                  </label>
                </div>

                <div>
                  <input
                    type="radio"
                    id={`${test.audio}_${test.options[3]}`}
                    name={test.audio}
                    value={test.options[3]}
                    onClick={() => handleChoiceChange(test.audio, test.options[3])}
                    checked={test.choice === test.options[3]}
                    required
                  />
                  <label htmlFor={`${test.audio}_${test.options[3]}`}>
                    <img
                      className="image-option"
                      src={test.options[3]}
                      alt={test.meanings[3]}
                    />
                  </label>
                </div>
              </div>
            </div>
          ))}
        </div>
        <button
          type="submit"
          className="button"
        >
          Finish
        </button>

        {isFinished && (
          <div className="result">
            <h2>Results</h2>
            <h3>{`Your score: ${score}`}</h3>
            <p>{`Your result ${res}`}</p>
            <p>{`Your detailed result ${res}`}</p>
          </div>
        )}
      </form>
    </>
  );
};
