import React, { useState } from 'react';
import classNames from 'classnames';
import ReactAudioPlayer from 'react-audio-player';
import audio1 from '../../audio/Test_01/L_0.mp3';
import audio2 from '../../audio/Test_02/R4_0.mp3';
import audio3 from '../../audio/Test_03/L5_-05.mp3';
import audio4 from '../../audio/Test_04/R6_-05.mp3';
import audio5 from '../../audio/Test_05/L7_-10.mp3';
import audio6 from '../../audio/Test_06/R8_-10.mp3';
import audio7 from '../../audio/Test_07/L1_-15.mp3';
import audio8 from '../../audio/Test_01/R_-15.mp3';
import audio9 from '../../audio/Test_02/L4_-20.mp3';
import audio10 from '../../audio/Test_03/R5_-20.mp3';
import audio11 from '../../audio/Test_04/L6_-25.mp3';
import audio12 from '../../audio/Test_05/R7_-25.mp3';
import audio13 from '../../audio/Test_06/L8_-30.mp3';
import audio14 from '../../audio/Test_07/R1_-30.mp3';
import audio15 from '../../audio/Test_01/L_-35.mp3';
import audio16 from '../../audio/Test_02/R4_-35.mp3';
import noAudio1 from '../../audio/Test_01/No_audio_1.mp3';
import noAudio2 from '../../audio/Test_01/No_audio_2.mp3';
import noAudio3 from '../../audio/Test_01/No_audio_3.mp3';
import noAudio4 from '../../audio/Test_01/No_audio_4.mp3';

// import './Test2.scss';

export const Test2:React.FC = () => {
  const test2 = [
    {
      audio: audio1,
      range: 0,
      options: ['Вважай', 'Врожай', 'Вгадай', 'Не чую'],
      correct: 'Вважай',
      choice: '',
    },
    {
      audio: audio2,
      range: 0,
      options: ['Сховай', 'Чекай', 'Читай', 'Не чую'],
      correct: 'Чекай',
      choice: '',
    },
    {
      audio: audio3,
      range: -5,
      options: ['Не ламай', 'Нехай', 'Не чіпай', 'Не чую'],
      correct: 'Не чіпай',
      choice: '',
    },
    {
      audio: audio4,
      range: -5,
      options: ['Сідай', 'Тікай', 'Рушай', 'Не чую'],
      correct: 'Тікай',
      choice: '',
    },
    {
      audio: audio5,
      range: -10,
      options: ['Хапай', 'Рушай', 'Сідай', 'Не чую'],
      correct: 'Сідай',
      choice: '',
    },
    {
      audio: audio6,
      range: -10,
      options: ['Зривай', 'Зіграй', 'Не займай', 'Не чую'],
      correct: 'Зіграй',
      choice: '',
    },
    {
      audio: audio7,
      range: -15,
      options: ['Кидай', 'Китай', 'Копай', 'Не чую'],
      correct: 'Кидай',
      choice: '',
    },
    {
      audio: audio8,
      range: -15,
      options: ['Вмикай', 'Вдягай', 'Вважай', 'Не чую'],
      correct: 'Вважай',
      choice: '',
    },
    {
      audio: audio9,
      range: -20,
      options: ['Чекай', 'Співай', 'Лягай ', 'Не чую'],
      correct: 'Чекай',
      choice: '',
    },
    {
      audio: audio10,
      range: -20,
      options: ['Не чіпай', 'Не ховай', 'Не тікай', 'Не чую'],
      correct: 'Не чіпай',
      choice: '',
    },
    {
      audio: audio11,
      range: -25,
      options: ['Лишай', 'Кидай', 'Тікай', 'Не чую'],
      correct: 'Тікай',
      choice: '',
    },
    {
      audio: noAudio1,
      range: 0,
      options: ['Дай', 'Грай', 'Знай', 'Не чую'],
      correct: 'Не чую',
      choice: '',
    },
    {
      audio: noAudio2,
      range: 0,
      options: ['Читай', 'Чіпай', 'Чекай', 'Не чую'],
      correct: 'Не чую',
      choice: '',
    },
    {
      audio: audio12,
      range: -25,
      options: ['Сховай', 'Сідай', 'Співай', 'Не чую'],
      correct: 'Сідай',
      choice: '',
    },
    {
      audio: audio13,
      range: -30,
      options: ['Зіграй', 'Джедай', 'Давай', 'Не чую'],
      correct: 'Зіграй',
      choice: '',
    },
    {
      audio: audio14,
      range: -30,
      options: ['Кусай', 'Копай', 'Кидай', 'Не чую'],
      correct: 'Кидай',
      choice: '',
    },
    {
      audio: noAudio3,
      range: 0,
      options: ['Пхай', 'Знай', 'Дбай', 'Не чую'],
      correct: 'Не чую',
      choice: '',
    },
    {
      audio: audio15,
      range: -35,
      options: ['Хитай', 'Вдавай', 'Вважай', 'Не чую'],
      correct: 'Вважай',
      choice: '',
    },
    {
      audio: audio16,
      range: -35,
      options: ['Шукай', 'Чекай', 'Чіпай', 'Не чую'],
      correct: 'Чекай',
      choice: '',
    },
    {
      audio: noAudio4,
      range: 0,
      options: ['Випити', 'Вирити', 'Вимити', 'Не чую'],
      correct: 'Не чую',
      choice: '',
    },
  ];

  const [tests, setTests] = useState(test2);

  const handleChoiceChange = (audio: string, choice: string) => {
    const newArr = [...tests].map((test) => {
      if (test.audio === audio) {
        if (test.choice.localeCompare(choice) !== 0) {
          test.choice = choice;
        } else {
          test.choice = '';
        }
      }

      return test;
    });

    setTests(newArr);
  };

  const [isFinished, setIsFinished] = useState(false);
  const [score, setScore] = useState(0);
  const [res, setRes] = useState('');

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    const sumWithInitial = tests.reduce(
      (accumulator, cur) => {
        if (cur.choice === cur.correct) {
          return accumulator + 1;
        }

        return accumulator;
      },
      0,
    );

    const tempL = [10];
    const tempR = [10];

    for (const el of tests) {
      if (el.choice === el.correct && el.correct === 'L') {
        tempL.push(el.range);
      }

      if (el.choice === el.correct && el.correct === 'R') {
        tempR.push(el.range);
      }
    }

    setRes(`Ви чуєте лівим вухом при рівні ${Math.min(...tempL)} та правим ${Math.min(...tempR)} в залежності від звичайного голосу`);

    setScore(sumWithInitial);
    setIsFinished(true);
  };

  return (
    <>
      <h2>Test 2</h2>

      <form className="form" onSubmit={handleSubmit}>
        <div className="form_section">
          <h3>Прослухайте записи і позначте що ви чуєте</h3>
          {tests.map(test => (
            <div
              className={classNames(
                'form_section__question',
                { form_section__question__wrong: test.correct !== test.choice && isFinished },
                { form_section__question__right: test.correct === test.choice && isFinished },
              )}
              key={test.audio}
            >
              <ReactAudioPlayer
                src={test.audio}
                controls
              />

              <div className="form_section__question__answers">
                <div>
                  <input
                    type="radio"
                    id={`${test.audio}_${test.options[0]}`}
                    name={test.audio}
                    value={test.options[0]}
                    onClick={() => handleChoiceChange(test.audio, test.options[0])}
                    checked={test.choice === test.options[0]}
                    required
                  />
                  <label htmlFor={`${test.audio}_${test.options[0]}`}>{test.options[0]}</label>
                </div>

                <div>
                  <input
                    type="radio"
                    id={`${test.audio}_${test.options[1]}`}
                    name={test.audio}
                    value={test.options[1]}
                    onClick={() => handleChoiceChange(test.audio, test.options[1])}
                    checked={test.choice === test.options[1]}
                    required
                  />
                  <label htmlFor={`${test.audio}_${test.options[1]}`}>{test.options[1]}</label>
                </div>

                <div>
                  <input
                    type="radio"
                    id={`${test.audio}_${test.options[2]}`}
                    name={test.audio}
                    value={test.options[2]}
                    onClick={() => handleChoiceChange(test.audio, test.options[2])}
                    checked={test.choice === test.options[2]}
                    required
                  />
                  <label htmlFor={`${test.audio}_${test.options[2]}`}>{test.options[2]}</label>
                </div>

                <div>
                  <input
                    type="radio"
                    id={`${test.audio}_${test.options[3]}`}
                    name={test.audio}
                    value={test.options[3]}
                    onClick={() => handleChoiceChange(test.audio, test.options[3])}
                    checked={test.choice === test.options[3]}
                    required
                  />
                  <label htmlFor={`${test.audio}_${test.options[3]}`}>{test.options[3]}</label>
                </div>
              </div>
            </div>
          ))}
        </div>
        <button
          type="submit"
          className="button"
        >
          Finish
        </button>

        {isFinished && (
          <div className="result">
            <h2>Results</h2>
            <h3>{`Your score: ${score}`}</h3>
            <p>{`Your result ${res}`}</p>
            <p>{`Your detailed result ${res}`}</p>
          </div>
        )}
      </form>
    </>
  );
};
