import React, { useState } from 'react';
import classNames from 'classnames';
import ReactAudioPlayer from 'react-audio-player';
import audio1 from '../../audio/Test_08/LR3_32_0.mp3'; // стій
import audio2 from '../../audio/Test_09/LR9_32_0.mp3'; // стрій
import audio3 from '../../audio/Test_10/LR10_32_-05.mp3'; // бій
import audio4 from '../../audio/Test_11/LR11_32_-05.mp3'; // дій
import audio5 from '../../audio/Test_12/LR12_32_-10.mp3'; // змій
import audio6 from '../../audio/Test_13/LR14_28_-10.mp3'; // командир
import audio7 from '../../audio/Test_14/LR16_28_-15.mp3'; // дезертир
import audio8 from '../../audio/Test_15/LR15_28_-15.mp3'; // поводир
import audio9 from '../../audio/Test_16/LR17_28_-20.mp3'; // богатир
import audio10 from '../../audio/Test_08/LR3_32_-20.mp3'; // стій
import audio11 from '../../audio/Test_09/LR9_32_-25.mp3'; // стрій
import audio12 from '../../audio/Test_10/LR10_32_-25.mp3'; // бій
import audio13 from '../../audio/Test_11/LR11_32_-30.mp3'; // дій
import audio14 from '../../audio/Test_12/LR12_32_-30.mp3'; // змій
import audio15 from '../../audio/Test_13/LR14_28_-35.mp3'; // командир
import audio16 from '../../audio/Test_14/LR16_28_-35.mp3'; // дезертир
import noAudio1 from '../../audio/Test_01/No_audio_1.mp3';
import noAudio2 from '../../audio/Test_01/No_audio_2.mp3';
import noAudio3 from '../../audio/Test_01/No_audio_3.mp3';
import noAudio4 from '../../audio/Test_01/No_audio_4.mp3';

// import './Test3.scss';

export const Test3:React.FC = () => {
  const test3 = [
    {
      audio: audio1,
      range: 0,
      options: ['Бій', 'Cтій', 'Дій', 'Не чую'],
      correct: 'Cтій',
      choice: '',
    },
    {
      audio: audio2,
      range: 0,
      options: ['Cтій', 'Дій', 'Cтрій', 'Не чую'],
      correct: 'Cтрій',
      choice: '',
    },
    {
      audio: audio3,
      range: -5,
      options: ['Бій', 'Цій', 'Свій', 'Не чую'],
      correct: 'Бій',
      choice: '',
    },
    {
      audio: audio4,
      range: -5,
      options: ['Мій', 'Бій', 'Дій', 'Не чую'],
      correct: 'Дій',
      choice: '',
    },
    {
      audio: audio5,
      range: -10,
      options: ['Гній', 'Змій', 'Свій', 'Не чую'],
      correct: 'Змій',
      choice: '',
    },
    {
      audio: audio6,
      range: -10,
      options: ['Касир', 'Богатир', 'Командир', 'Не чую'],
      correct: 'Командир',
      choice: '',
    },
    {
      audio: audio7,
      range: -15,
      options: ['Монастир', 'Дезертир', 'Командир', 'Не чую'],
      correct: 'Дезертир',
      choice: '',
    },
    {
      audio: audio8,
      range: -15,
      options: ['Інжир', 'Еліксир', 'Поводир', 'Не чую'],
      correct: 'Поводир',
      choice: '',
    },
    {
      audio: audio9,
      range: -20,
      options: ['Дезертир', 'Богатир', 'Поводир', 'Не чую'],
      correct: 'Богатир',
      choice: '',
    },
    {
      audio: audio10,
      range: -20,
      options: ['Водій', 'Стій', 'Змій', 'Не чую'],
      correct: 'Стій',
      choice: '',
    },
    {
      audio: audio11,
      range: -25,
      options: ['Рушій', 'Радій', 'Стрій', 'Не чую'],
      correct: 'Стрій',
      choice: '',
    },
    {
      audio: noAudio1,
      range: 0,
      options: ['Дай', 'Грай', 'Знай', 'Не чую'],
      correct: 'Не чую',
      choice: '',
    },
    {
      audio: noAudio2,
      range: 0,
      options: ['Читай', 'Чіпай', 'Чекай', 'Не чую'],
      correct: 'Не чую',
      choice: '',
    },
    {
      audio: audio12,
      range: -25,
      options: ['Радій', 'Відбій', 'Бій', 'Не чую'],
      correct: 'Бій',
      choice: '',
    },
    {
      audio: audio13,
      range: -30,
      options: ['Дій', 'Радій', 'Змій', 'Не чую'],
      correct: 'Дій',
      choice: '',
    },
    {
      audio: audio14,
      range: -30,
      options: ['Дій', 'Рушій', 'Змій', 'Не чую'],
      correct: 'Змій',
      choice: '',
    },
    {
      audio: noAudio3,
      range: 0,
      options: ['Пхай', 'Знай', 'Дбай', 'Не чую'],
      correct: 'Не чую',
      choice: '',
    },
    {
      audio: audio15,
      range: -35,
      options: ['Дезертир', 'Інжир', 'Командир', 'Не чую'],
      correct: 'Командир',
      choice: '',
    },
    {
      audio: audio16,
      range: -35,
      options: ['Касир', 'Дезертир', 'Богатир', 'Не чую'],
      correct: 'Дезертир',
      choice: '',
    },
    {
      audio: noAudio4,
      range: 0,
      options: ['Випити', 'Вирити', 'Вимити', 'Не чую'],
      correct: 'Не чую',
      choice: '',
    },
  ];

  const [tests, setTests] = useState(test3);

  const handleChoiceChange = (audio: string, choice: string) => {
    const newArr = [...tests].map((test) => {
      if (test.audio === audio) {
        if (test.choice.localeCompare(choice) !== 0) {
          test.choice = choice;
        } else {
          test.choice = '';
        }
      }

      return test;
    });

    setTests(newArr);
  };

  const [isFinished, setIsFinished] = useState(false);
  const [score, setScore] = useState(0);
  const [res, setRes] = useState('');

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    const sumWithInitial = tests.reduce(
      (accumulator, cur) => {
        if (cur.choice === cur.correct) {
          return accumulator + 1;
        }

        return accumulator;
      },
      0,
    );

    const tempL = [10];
    const tempR = [10];

    for (const el of tests) {
      if (el.choice === el.correct && el.correct === 'L') {
        tempL.push(el.range);
      }

      if (el.choice === el.correct && el.correct === 'R') {
        tempR.push(el.range);
      }
    }

    setRes(`Ви чуєте лівим вухом при рівні ${Math.min(...tempL)} та правим ${Math.min(...tempR)} в залежності від звичайного голосу`);

    setScore(sumWithInitial);
    setIsFinished(true);
  };

  return (
    <>
      <h2>Test 3</h2>

      <form className="form" onSubmit={handleSubmit}>
        <div className="form_section">
          <h3>Прослухайте записи і позначте що ви чуєте</h3>
          {tests.map(test => (
            <div
              className={classNames(
                'form_section__question',
                { form_section__question__wrong: test.correct !== test.choice && isFinished },
                { form_section__question__right: test.correct === test.choice && isFinished },
              )}
              key={test.audio}
            >
              <ReactAudioPlayer
                src={test.audio}
                controls
              />

              <div className="form_section__question__answers">
                <div>
                  <input
                    type="radio"
                    id={`${test.audio}_${test.options[0]}`}
                    name={test.audio}
                    value={test.options[0]}
                    onClick={() => handleChoiceChange(test.audio, test.options[0])}
                    checked={test.choice === test.options[0]}
                    required
                  />
                  <label htmlFor={`${test.audio}_${test.options[0]}`}>{test.options[0]}</label>
                </div>

                <div>
                  <input
                    type="radio"
                    id={`${test.audio}_${test.options[1]}`}
                    name={test.audio}
                    value={test.options[1]}
                    onClick={() => handleChoiceChange(test.audio, test.options[1])}
                    checked={test.choice === test.options[1]}
                    required
                  />
                  <label htmlFor={`${test.audio}_${test.options[1]}`}>{test.options[1]}</label>
                </div>

                <div>
                  <input
                    type="radio"
                    id={`${test.audio}_${test.options[2]}`}
                    name={test.audio}
                    value={test.options[2]}
                    onClick={() => handleChoiceChange(test.audio, test.options[2])}
                    checked={test.choice === test.options[2]}
                    required
                  />
                  <label htmlFor={`${test.audio}_${test.options[2]}`}>{test.options[2]}</label>
                </div>

                <div>
                  <input
                    type="radio"
                    id={`${test.audio}_${test.options[3]}`}
                    name={test.audio}
                    value={test.options[3]}
                    onClick={() => handleChoiceChange(test.audio, test.options[3])}
                    checked={test.choice === test.options[3]}
                    required
                  />
                  <label htmlFor={`${test.audio}_${test.options[3]}`}>{test.options[3]}</label>
                </div>
              </div>
            </div>
          ))}
        </div>
        <button
          type="submit"
          className="button"
        >
          Finish
        </button>

        {isFinished && (
          <div className="result">
            <h2>Results</h2>
            <h3>{`Your score: ${score}`}</h3>
            <p>{`Your result ${res}`}</p>
            <p>{`Your detailed result ${res}`}</p>
          </div>
        )}
      </form>
    </>
  );
};
